.all_order_inf {
  margin-top: 26px;
}

.order_inf {
  display: flex;
  justify-content: space-between;
}

.order_inf:not(:first-child) {
  margin-top: 16px;
}

.order_inf_hidden {
  display: none;
}

.inf_name {
  display: flex;
  align-items: flex-end;
  width: fit-content;
  font-weight: 300;
  font-size: 14px;
  line-height: 16px;
  color: #121212;
  font-family: "Roboto";
}

.inf_value {
  width: fit-content;
  font-family: "Roboto";
  font-weight: 300;
  font-size: 14px;
  line-height: 16px;
  text-align: right;
  color: #999999;
}

.dots_style {
  flex: 1;
  border-bottom: 2px dotted #999999;
  margin-left: 12px;
  margin-right: 12px;
}

.all_order_inf {
  margin-top: 26px;
}

.order_price_inf {
  margin-top: 32px;
  font-size: 16px;
  line-height: 16px;
  color: #121212;
  display: flex;
}

.order_price_header {
  font-family: "Roboto";
  font-weight: 500;
}

.order_price {
  font-family: "Roboto";
  font-weight: 400;
}

.order_inf_btn_active {
  background: linear-gradient(90deg, #0ec261 2.61%, #039f67 112.6%);
}

.order_inf_btn_disabled {
  background: #eeeeee;
}

.order_inf_btn:hover {
  filter: brightness(90%);
}

.order_inf_btn:active {
  filter: brightness(80%);
}

.mobile_order_inf_btn {
  display: none;
}
