.input_form {
  margin-top: 32px;
  width: fit-content;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
}

@media (max-width: 540px) {
  .input_form {
    margin-left: 8%;
  }
}

.map_part {
  height: calc(100vh - 130px - 85px);
  margin-top: 48px;
}

@media (max-width: 1160px) {
  .map_part {
    display: flex;
    flex-direction: column;
  }
}

@media (max-width: 540px) {
  .map_part {
    align-items: center;
    width: 100vw;
    margin-top: 24px;
  }
}

.map_header {
  font-family: "Roboto";
  font-weight: 300;
  margin-bottom: 16px;
}
