.img_form {
  position: absolute;
  width: calc(100vw - 64px - 384px - 128px);
  height: calc(100vh - 130px);
  display: flex;
  justify-content: center;
  align-items: center;
}

@media (max-width: 1160px) {
  .img_form {
    width: 90vw;
    left: 86px;
  }
}

@media (max-width: 540px) {
  .img_form {
    width: 100vw;
    left: auto;
    padding: 0;
  }
}

.img {
  width: 50px;
  height: 50px;
  animation: spin 2s infinite linear;
  background-image: url("../../assets/loading.svg");
}

@keyframes spin {
  100% {
    transform: rotate(360deg);
  }
}
